import React from "react";
import { ZeditorTreeHOC } from "zerod";
import { Input, message } from "antd";
import HOC from "@/HOC/load-HOC.js";
const { AmdDocHOC, AshowDemoHOC } = HOC;
import doc from "zerod/components/ZeditorTreeHOC/doc.md";
import pageHeader from "@/views/Main/component-doc/ZpageHeader-doc/pageHeader";
import area from "@/mock/area";
import { deepCopy } from "zerod/components/zTool";
const showDemo = function(Component) {
	return <Component />;
};
export default AmdDocHOC(doc, {
	demo1: function() {
		const config = {
			insertLocation: "mainModal",
			pageHeader: {
				show: true,
				...pageHeader,
			},
			searchForm: {
				// array>[object] | null，如果是null则不显示搜索表单
				items: [
					{
						key: "serviceCode",
						label: "服务编码",
						render: (form) => {
							return <Input placeholder="请输入内容" />;
						},
					},
					{
						key: "serviceName",
						label: "服务名称",
						render: (form) => {
							return <Input placeholder="请输入内容" />;
						},
					},
				],
			},
			tree: {
				treeProps:{
					// defaultExpandAll:true,
					draggable:true,
					onDragEnd:(detail)=>{
						console.log("onDragEnd")
					},
					onDragEnter:()=>{
						console.log("onDragEnter")
					},
					onDragLeave:()=>{
						console.log("onDragLeave")
					},
					onDragOver:()=>{
						console.log("onDragOver")
					},
					onDragStart:()=>{
						console.log("onDragStart")
					},
					onDrop:(info,dropData,done,tool)=>{
						done();
					},
				},
				treeDataKeys: { id: "areaId" },
				panelHeader: "树",
				// 是否显示新建按钮
				showAddBtn: true,
				// 新建按钮权限控制代码
				addBtnPermCode: "",
				addPageRender: (panel) => {
					return (
						<div className="z-panel z-text-center z-margin-bottom-20">
							<div className="z-panel-body">addPageRender 函数渲染的内容</div>
						</div>
					);
				},
				// 是否显示新增子节点按钮
				showAddChildBtn: true,
				// 新建按钮权限控制代码
				addBtnPermCode: "",
				// 新增子节点页面渲染模板
				addChildPageRender: (panel) => {
					return (
						<div className="z-panel z-text-center z-margin-bottom-20">
							<div className="z-panel-body">addChildPageRender 函数渲染的内容</div>
						</div>
					);
				},
				// 是否显示详情按钮
				showDetailBtn: true,
				// 详情按钮权限控制代码
				detailBtnPermCode: "",
				detailPageRender: (record) => {
					return (
						<div className="z-panel z-text-center z-margin-bottom-20">
							<div className="z-panel-body">detailPageRender 函数渲染的内容</div>
						</div>
					);
				},
				// 是否显示修改按钮
				showUpdateBtn: true,
				// 修改按钮权限控制代码
				updateBtnPermCod: "",
				updatePageRender: (record, panel) => {
					return (
						<div className="z-panel z-text-center z-margin-bottom-20">
							<div className="z-panel-body">updatePageRender 函数渲染的内容</div>
						</div>
					);
				},
				// 是否显示删除按钮
				showDeleteBtn: true,
				// 删除按钮权限控制代码
				deleteBtnPermCod: "",
				// moreBtnType:"menu",
				//更多操作按钮的map数据
				moreBtnMap: [
					{
						key: "0",
						name: "默认的按钮",
					},
				],
				//更多操作按钮的的点击事件
				onMoreBtnClick: (item, record) => {
					message.success(`您当前点击的是[${record.name}]这条数据`);
				},
				// 获取列表数据的后台接口函数，其必须内部返回Promise
				treeApiInterface: (query) => {
					// return api.area.getChildList({ id: "root" }).then((re) => {
					// 	return { data: re.list };
					// });
					return new Promise((resolve) => {
						setTimeout(() => {
							resolve({ data: deepCopy(area.data) });
						}, 500);
					});
				},
				childApiInterface: false,
				// childApiInterface: (query) => {
				// 	return api.area.getChildList({ id: query.areaId }).then((re) => {
				// 		return { data: re.list };
				// 	});
				// },
				// 删除按钮的后台接口函数，其必须内部返回Promise
				deleteApiInterface: (data) => {
					return Promise.resolve();
				},
				// exportSomething: (obj) => (listExports = obj),
				panelBeforeRender: (obj) => {
					return (
						<div className="z-panel z-text-center z-margin-bottom-20">
							<div className="z-panel-body">panelBeforeRender 函数渲染的内容</div>
						</div>
					);
				},
				panelAfterRender: (obj) => {
					return (
						<div className="z-panel z-margin-top-20 z-text-center">
							<div className="z-panel-body">panelAfterRender 函数渲染的内容</div>
						</div>
					);
				},
				moreContentRender: (obj) => {
					return (
						<div className="z-panel is-panel-border z-margin-top-20 z-text-center">
							<div className="z-panel-body">moreContentRender 函数渲染的内容</div>
						</div>
					);
				},
			},
		};
		const MyScript = AshowDemoHOC(showDemo(ZeditorTreeHOC(config)));
		return <MyScript />;
	},
});
